import React from "react";
import ContextSteps from "../app/ContextSteps";
import SelectApi from "./SelectApi";
import swap_icon from "../../assets/Swap.png";
import add_icon from "../../assets/Add.png";
import remove_icon from "../../assets/Remove.png";
import normal_icon from "../../assets/Normal.png";
import work_icon from "../../assets/Work.png";
import school_icon from "../../assets/School.png";
import doctor_icon from "../../assets/Doctor.png";
import freetime_icon from "../../assets/Bike.png";


class PlannerContextPID extends React.Component {
  constructor(props) {
    super(props);
    //STATE
    this.state = {
      from: "",
      to: "",
      suggestions: undefined,
      via: false,
      date: "",
      time: "",
      directPath: false,
      fastestRoute: false,
      numTransfers: "",
      traveler_type: "pouze pe chze",
      special: "bez dalch poteb",
      context: "none"
    };
    //BINDINGS
    this.initialTimeDateSetup = this.initialTimeDateSetup.bind(this);
    this.handleFromInput = this.handleFromInput.bind(this);
    this.handleViaInput = this.handleViaInput.bind(this);
    this.handleToInput = this.handleToInput.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleTimeAndDate = this.handleTimeAndDate.bind(this);
    this.drawSuggestions = this.drawSuggestions.bind(this);
    this.updateSelectedApi = this.updateSelectedApi.bind(this);
    this.changeTransit = this.changeTransit.bind(this);
    this.updateInputValues = this.updateInputValues.bind(this);
    this.swapInputFields = this.swapInputFields.bind(this);
    this.updateOptions = this.updateOptions.bind(this);
    this.getOptions = this.getOptions.bind(this);
  }

  initialTimeDateSetup() {
    var date = new Date();
    var initApiTime =
      date.getFullYear() +
      "" +
      (date.getMonth() + 1 < 10
        ? "0" + (date.getMonth() + 1)
        : date.getMonth() + 1) +
      "" +
      (date.getDate() < 10 ? "0" + date.getDate() : date.getDate()) +
      "T" +
      (date.getHours() < 10 ? "0" + date.getHours() : date.getHours()) +
      "" +
      (date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes()) +
      "00";
    var currentDate =
      date.getFullYear() +
      "-" +
      (date.getMonth() + 1 < 10
        ? "0" + (date.getMonth() + 1)
        : date.getMonth() + 1) +
      "-" +
      (date.getDate() < 10 ? "0" + date.getDate() : date.getDate());
    var currentTime =
      (date.getHours() < 10 ? "0" + date.getHours() : date.getHours()) +
      ":" +
      (date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes());
    document.getElementById("input_date").value = currentDate;
    document.getElementById("input_time").value = currentTime;
    this.setState({ date: currentDate, time: currentTime });
    this.props.timedate(initApiTime);
  }

  componentDidMount() {
    this.initialTimeDateSetup();
    let options = document.getElementsByClassName("options-box");
    for (let i = 1; i < options[0].children.length; i++) {
      options[0].children[i].style.display = "none";
    }
  }

  updateSelectedApi(api) {
    this.props.setApi(api);
  }

  handleFromInput(e) {
    this.setState({ from: e.target.value });
    this.props.start(e.target.value);
    this.props.startId(e.target.value);
    if (e.target.value.length < 1) {
      e.target.setAttribute("warning", "true");
    } else {
      e.target.setAttribute("warning", "false");
    }
  }

  handleViaInput(e) {
    this.props.via(e.target.value);
    this.props.viaId(e.target.value);
  }

  handleToInput(e) {
    this.setState({ to: e.target.value });
    this.props.end(e.target.value);
    this.props.endId(e.target.value);
    if (e.target.value.length < 1) {
      e.target.setAttribute("warning", "true");
    } else {
      e.target.setAttribute("warning", "false");
    }
  }

  handleTimeAndDate(e) {
    //e.preventDefault();
    let timeInput = document.getElementById("input_time");
    let dateInput = document.getElementById("input_date");
    this.setState({ date: timeInput.value, time: timeInput.value });
    var time = timeInput.value.substr(0, 2) + timeInput.value.substr(3, 2);
    var date =
      dateInput.value.substr(0, 4) +
      dateInput.value.substr(5, 2) +
      dateInput.value.substr(8, 2);
    let inputTimeDate = date + "T" + time;
    this.props.timedate(inputTimeDate);
  }

  drawSuggestions() {
    var p = document.getElementById("suggestions");
    p.appendChild(document.createElement("ul"));
    for (let i = 0; i < this.state.suggestions.length; i++) {
      let li = document.createElement("li");
      li.appendChild(document.createTextNode(this.state.suggestions[i]));
      p.childNodes[0].appendChild(li);
    }
  }

  updateInputValues() {
    var from = document.getElementById("input_from");
    var to = document.getElementById("input_to");
    this.props.start(from.value);
    this.props.end(to.value);
  }

  updateOptions() {
    this.getOptions();
  }

  handleSubmit(e) {
    this.setState({ vialFrom: this.state.from, vialTo: this.state.to });
    if (
      document.getElementById("input_from").value.length < 1 ||
      document.getElementById("input_to").value.length < 1
    ) {
      e.preventDefault();
      document.getElementById("input_from").setAttribute("warning", "true");
      document.getElementById("input_to").setAttribute("warning", "true");
    } else {
      e.preventDefault();
      if (this.props.update !== undefined) {
        this.updateInputValues();
        this.props.update();
      } else {
        this.props.ready(true);
      }
    }
  }

  changeTransit(e) {
    this.getOptions();
    var viaSection = document.getElementById("via");
    if (this.state.via === false) {
      viaSection.setAttribute("active", "true");
      let addViaEls = document.getElementsByClassName("addVia");
      let remViaEls = document.getElementsByClassName("removeVia");
      addViaEls[0].style.display = "none";
      remViaEls[0].style.display = "inline-block";
      this.setState({ via: true });
    } else {
      document.getElementById("input_via").value = "";
      this.props.via(document.getElementById("input_via").value);
      viaSection.setAttribute("active", "false");
      let addViaEls = document.getElementsByClassName("addVia");
      let remViaEls = document.getElementsByClassName("removeVia");
      addViaEls[0].style.display = "inline-block";
      remViaEls[0].style.display = "none";
      this.setState({ via: false });
    }
  }

  swapInputFields() {
    this.getOptions();
    var fromOld = document.getElementById("input_from").value;
    var to = document.getElementById("input_to");
    var from = document.getElementById("input_from");
    from.value = to.value;
    to.value = fromOld;
    this.props.start(from.value);
    this.props.startId(from.value);
    this.props.end(to.value);
    this.props.endId(to.value);
    this.setState({ from: from.value });
    this.setState({ to: to.value });
  }

  getOptions() {
    console.log("Options:")
    var options = {
      travel_type: document.querySelector('input[name="travel_type"]:checked').getAttribute("id"),
      means: document.querySelector('input[name="means"]:checked').getAttribute("id"),
      special: document.querySelector('input[name="special"]:checked').getAttribute("id"),
      weather: document.querySelector('input[name="weather"]:checked').getAttribute("id")
    };
    console.log(options)
    if (options.directPath) {
      this.setState({ directPath: true });
    }
    if (options.fastestRoute) {
      this.setState({ fastestRoute: true });
    }
    if (options.numTransfers > 0) {
      this.setState({ numTransfers: options.numTransfers });
    } else {
      this.setState({ numTransfers: "neomezenm potem" });
    }
    if (options.travel_type === "normal") {
      this.setState({ traveler_type: "pouze pe chze" });
    }
    if (options.travel_type === "work") {
      this.setState({ travel_type: "kola nebo kolobky" });
    }
    if (options.travel_type === "school") {
      this.setState({ traveler_type: "osobnho automobilu" });
    }
    if (options.travel_type === "freetime") {
      this.setState({ traveler_type: "osobnho automobilu" });
    }
    // MEANS
    if (options.means === "nopref") {
      this.setState({ traveler_type: "osobnho automobilu" });
    }
    if (options.means === "public_transport") {
      this.setState({ traveler_type: "osobnho automobilu" });
    }
    if (options.means === "walker") {
      this.setState({ traveler_type: "osobnho automobilu" });
    }
    if (options.means === "cyclist") {
      this.setState({ traveler_type: "osobnho automobilu" });
    }
    // SPECIAL REQUIREMENTS
    if (options.special === "standard") {
      this.setState({ traveler_type: "osobnho automobilu" });
    }
    if (options.special === "slow_walker") {
      this.setState({ traveler_type: "osobnho automobilu" });
    }
    if (options.special === "wheelchair") {
      this.setState({ traveler_type: "osobnho automobilu" });
    }
    if (options.special === "luggage") {
      this.setState({ traveler_type: "osobnho automobilu" });
    }
    if (options.special === "cyclist") {
      this.setState({ traveler_type: "osobnho automobilu" });
    }
    // WEATHER
    if (options.weather === "sunny") {
      this.setState({ traveler_type: "osobnho automobilu" });
    }
    if (options.weather === "overcast") {
      this.setState({ traveler_type: "osobnho automobilu" });
    }
    if (options.weather === "overcast_wet") {
      this.setState({ traveler_type: "osobnho automobilu" });
    }
    if (options.weather === "rain") {
      this.setState({ traveler_type: "osobnho automobilu" });
    }
    if (options.weather === "snow") {
      this.setState({ traveler_type: "osobnho automobilu" });
    }

    console.log("Writing:");
    console.log(options.traveler_type);
    console.log(options.means);
    console.log(options.special);
    this.props.getOptions(options);
  }

  render() {
    return (
      <form
        className="input"
        id="contextForm"
        action="/result"
        onSubmit={this.handleSubmit}
        onChange={this.getOptions}
      >
        <div className="options">
          <ContextSteps step={this.state.step}></ContextSteps>
          <div className="options-box">
            <div id="options-box-main" display="block">
              <div className="inputFieldDiv">
                <label className="textLabel">Odkud:</label>
                <input
                  className="locations"
                  id="input_from"
                  type="form"
                  placeholder="Zadejte poten bod"
                  onChange={this.handleFromInput}
                ></input>
              </div>
              <p id="suggestions"></p>
              <div className="inputFieldDiv" id="via" active="false">
                <label className="addVia" onClick={this.changeTransit}>
                  <img
                    className="viaBtn"
                    src={add_icon}
                    alt="+"
                    title="Pidat prjezdn bod"
                  ></img>
                  <label id="addViaText">Pidat prjezd</label>
                </label>
                <label className="removeVia" style={{ display: "none" }}>
                  <img
                    className="viaBtn"
                    src={remove_icon}
                    alt=""
                    onClick={this.changeTransit}
                    title="Odebrat prjezdn bod"
                  ></img>
                  <input
                    className="locations"
                    id="input_via"
                    type="form"
                    placeholder="Zadejte prjezdov bod"
                    onChange={this.handleViaInput}
                  ></input>
                </label>
                <label id="swapper" onClick={this.swapInputFields}>
                  <img
                    className="clickable"
                    src={swap_icon}
                    alt="[SWAP]"
                    title="Prohodit Start a Cl"
                  ></img>
                </label>
              </div>
              <div className="inputFieldDiv">
                <label className="textLabel">Kam:</label>
                <input
                  className="locations"
                  id="input_to"
                  type="form"
                  placeholder="Zadejte clov bod"
                  onChange={this.handleToInput}
                ></input>
              </div>
              <div className="box" id="timeBox">
                <div className="inputFieldDiv">
                  <label className="textLabel">Datum a as:</label>
                  <input
                    id="input_date"
                    type="date"
                    onChange={this.handleTimeAndDate}
                  ></input>
                  <input
                    id="input_time"
                    type="time"
                    onChange={this.handleTimeAndDate}
                  ></input>
                </div>
              </div>
              <div className="directPathBox">
                <label className="inputFieldDiv">
                  <input
                    type="checkbox"
                    id="directPath"
                    onChange={this.getOptions}
                  ></input>
                  Pouze pm spoj
                </label>
              </div>
            </div>
            <div
              className="contextOptionsSettings"
              id="options-box-parameters"
              display="none"
            >
              <label>
              <label>
                <input
                  type="radio"
                  name="travel_type"
                  className="route"
                  id="normal"
                  defaultChecked
                ></input>
                <img src={normal_icon}></img><br/>Normln
                <br />
              </label>
              <label>
                <input
                  type="radio"
                  name="travel_type"
                  className="route"
                  id="work"
                ></input>
                <img src={work_icon}></img><br/>
                Do prce
                <br />
              </label>
              <label>
                <input
                  type="radio"
                  name="travel_type"
                  className="route"
                  id="school"
                ></input>
                <img src={school_icon}></img><br/>
                Do koly
                <br />
              </label>
              <label>
                <input
                  type="radio"
                  name="travel_type"
                  className="route"
                  id="doctor"
                ></input>
                <img src={doctor_icon}></img><br/>
                Za lkaem
                <br />
              </label>
              <label>
                <input
                  type="radio"
                  name="travel_type"
                  className="route"
                  id="freetime"
                ></input>
                <img src={freetime_icon}></img><br/>
                Rekreace
                <br />
              </label>
              </label>
            </div>
            <div
              className="contextOptionsSettings"
              id="options-box-transport"
              display="none"
            >
              <label>
                <label>
                  <input
                    type="radio"
                    name="means"
                    className="traveler_type"
                    id="nopref"
                    defaultChecked
                  ></input>
                  Bez preference
                </label>
                <label>
                  <input
                    type="radio"
                    name="means"
                    className="traveler_type"
                    id="public_transport"
                  ></input>
                  MHD
                </label>
                <label>
                <input
                  type="radio"
                  name="means"
                  className="traveler_type"
                  id="walker"
                ></input>
                Pouze pe chze
              </label>
              <label>
                <input
                  type="radio"
                  name="means"
                  className="traveler_type"
                  id="cyclist"
                ></input>
                Kolo nebo kolobka
              </label>
              <label>
                <input
                  type="radio"
                  name="means"
                  className="traveler_type"
                  id="motorist"
                ></input>
                Osobn automobil
              </label>
              
              </label>
            </div>
            <div
              className="contextOptionsSettings"
              id="options-box-access"
              display="none"
            >
              <label>
              <label>
                <input
                  type="radio"
                  name="special"
                  className="traveler_type"
                  id="standard"
                  defaultChecked
                ></input>
                Bez omezen
              </label>
              <label>
                <input
                  type="radio"
                  name="special"
                  className="traveler_type"
                  id="slow_walker"
                ></input>
                S korkem
              </label>
              <label>
                <input
                  type="radio"
                  name="special"
                  className="traveler_type"
                  id="wheelchair"
                ></input>
                S vozkem
              </label>
              <label>
                <input
                  type="radio"
                  name="special"
                  className="traveler_type"
                  id="luggage"
                ></input>
                S tkmi zavazadly
              </label>
              <label>
                <input
                  type="radio"
                  name="special"
                  className="traveler_type"
                  id="cyclist"
                ></input>
                S kolem do MHD
              </label>
              </label>
            </div>
            <div
              className="contextOptionsSettings"
              id="options-box-summary"
              display="none"
            >
              <label>
                <label>
                  <input
                    type="radio"
                    name="weather"
                    className="traveler_type"
                    id="sunny"
                    defaultChecked
                  ></input>
                  Sluneno a sucho
                </label>
                <label>
                  <input
                    type="radio"
                    name="weather"
                    className="traveler_type"
                    id="overcast"
                  ></input>
                  Zataeno
                </label>
                <label>
                  <input
                    type="radio"
                    name="weather"
                    className="traveler_type"
                    id="overcast_wet"
                  ></input>
                  Zataeno a vlhko
                </label>
                <label>
                  <input
                    type="radio"
                    name="weather"
                    className="traveler_type"
                    id="rain"
                  ></input>
                  D
                </label>
                <label>
                  <input
                    type="radio"
                    name="weather"
                    className="traveler_type"
                    id="snow"
                  ></input>
                  Sn
                </label>
              </label>
            </div>
          </div>
        </div>
        <div className="submitBox">
          <SelectApi updateSelectedApi={this.updateSelectedApi}></SelectApi>
          <input
            type="submit"
            value="Vyhledat spojen"
            title="Vyhledat spoj dle zadanch hodnot"
          ></input>
        </div>
      </form>
    );
  }
}
export default PlannerContextPID;
