import React from "react";

class SelectElement extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
    //refs
    //bindings
    this.isDefaultChecked = this.isDefaultChecked.bind(this);
  }

  isDefaultChecked() {
    if (this.props.defaultChecked === true) {
      return true;
    } else {
      return false;
    }
  }

  render() {
    return (
      <React.Fragment>
        <label className="transfersLabel">{this.props.text}</label>
        <select
          type={this.props.type}
          name={this.props.name}
          className={this.props.className}
          id={this.props.id}
          defaultChecked={this.isDefaultChecked()}
        >
        </select>
      </React.Fragment>
    );
  }
}

export default SelectElement;
